

.App {
  text-align: center;
}

.center {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.push {
  height: 100px;
  margin-bottom: 50px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.header{
    background-color: skyblue;
    position: relative;
    width: 100%;
}

h1{
    text-align: center;
    font-size: 90px;
    font-weight: bold;
    padding: 90px 0 20px 0;
}

h2{
    text-align: center;
    font-size: 25px;
    padding-bottom: 40px;
}
.my-card {
    margin-top: 10px;
}

img.center {
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding: 10px;
    height: 200px;
    max-width: 200px;
}


.footer{
    background-color: darkgreen;
    width: 100%;
    position: relative;
    margin-bottom: -150px;
}

.footer p{
    color: white;
    text-align: center;
    padding: 10px 0;
    font-size: 15px;
}
